






































































































































.main-form {
    .el-form {
        .el-form-item {
            .el-input {
                width: 240px;
            }
            .el-select {
                width: 240px;
                .el-input {
                    width: 100%;
                }
            }
            .el-date-picker {
                width: 240px;
            }
            .el-textarea__inner {
                height: 100%;
            }
        }
    }
    .content-titel2 {
        margin: 0px 0px 20px 30px;
        padding-top: 30px;
        border-top: 1px solid #e8e8e8;
    }
}
.el-form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 90%;
    margin: 0 auto;
}
@media screen and (min-width: 1600px) {
    .el-form {
        width: 85%;
    }
}
@media screen and (min-width: 1800px) {
    .el-form {
        width: 80%;
    }
}
@media screen and (max-width: 1440px) {
    .el-form {
        width: 95%;
    }
}
@media screen and (max-width: 1300px) {
    .el-form {
        width: 95%;
    }
}
.el-form-item {
    width: 50%;
}
